<template>
	<view class="mySuggestion-warp">
		<view class="mySuggestion">
			<view class="mySuggestion-title">问题和意见</view>
			<textarea v-model="list" placeholder="请输入问题和意见,200字以内" class="mySuggestion-cnt" maxlength="200">{{list}}</textarea>
		</view>
		<view class="pullImg" @click="pullImg">上传照片</view>
	</view>
</template>

<script>
	export default{
		data () {
			return {
				list:'231313'
			}
		},
		methods:{
			pullImg () {
				uni.chooseImage({
				    count: 6, //默认9
				    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				    sourceType: ['album'], //从相册选择
				    success: function (res) {
				        console.log(JSON.stringify(res.tempFilePaths));
				    }
				});
			}
		}
	}
</script>

<style>
	.mySuggestion-warp{
		background-color: #F1F1F1;
		padding-top: 10rpx;
		padding-bottom: 10rpx;
	}
	.mySuggestion-title{
		width: 94%;
		margin-left: 3%;
		font-size: 28rpx;
	}
	.mySuggestion{
		background: #FFFFFF;
		width: 94%;
		margin-left: 3%;
		border-radius: 10rpx;
		padding: 20rpx 0rpx;
	}
	.mySuggestion-warp textarea{
		font-size: 28rpx;
		padding: 10rpx 10px;
		box-sizing: border-box;
	}
	.mySuggestion-cnt{
		margin-top: 10rpx;
		width: 94%;
		margin-left: 3%;
		border: 1px solid #F1F1F1;
		border-radius: 10rpx;
	}
</style>
